.display {
	grid-column: span 4;
	background-color: #0004;
	display: flex;
	justify-content:  flex-end;
	align-items: center;
	padding: 20px;
	font-size: 2.1em;
	overflow: hidden;
}

.button:active{
	background-color: #ccc;
}

.button.double{
	grid-column: span 2;
}

.button.triple{
	grid-column: span 3;
}

.button.operation{
	background-color: #fa8231;
	color: #fff;
}

.button.operation:active{
	background-color: #fa8231cc;
}